import { Avatar, Button, Box, List, ListItem, ListItemAvatar, ListItemSecondaryAction, ListItemText, TextField, withStyles } from '@material-ui/core'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { v4 } from 'uuid'
import { useFormik } from 'formik'
import { follow, unfollow } from 'store/slices/usersSlice'
import { useMediaQuery } from '@material-ui/core'

const UsersPage = (props) => {
  const matches = useMediaQuery('(min-width: 768px)')
  const form = useFormik({
    initialValues: {
      searchValue: ''
    },
    onSubmit(v) {
      
    }
  })

  const users = props.users
  const currentUser = users.find(u => u.id === props.auth.userId)

  const filteredUsersBySearchValue = users.filter(u => u.username.includes(form.values.searchValue))
  const filteredUsersWithoutCurrentUser = filteredUsersBySearchValue.filter(u => u.id !== currentUser?.id)

  const renderedUsers = () => {
    return filteredUsersWithoutCurrentUser.map(user => {
      let currentUserFollowedToUser =
        currentUser?.followings.find(f => f === user.id)

      return (
        <ListItem button key={v4()} style={{ padding: '5px 0' }}>
          <ListItemAvatar>
            <Avatar
              src={user.avatarUrl}
            />
          </ListItemAvatar>
          <ListItemText primary={user.username} />
          <ListItemSecondaryAction>
            {props.auth.isAuthenticated && (
              (currentUserFollowedToUser ? (
                <StyledUnfollowBtn
                  style={{ backgroundColor: 'lightgreen' }}
                  variant={'contained'}
                  onClick={() => props.unfollow({ from: currentUser.id, to: user.id })}
                  size={matches ? 'medium' : 'small'}
                >
                  unfollow
                </StyledUnfollowBtn>
              ) : (
                <StyledFollowBtn
                  color={'secondary'}
                  variant={'contained'}
                  onClick={() => props.follow({ from: currentUser.id, to: user.id })}
                  size={matches ? 'medium' : 'small'}
                >
                  follow
                </StyledFollowBtn>
              ))
            )}
            <Link to={`/profile/${user.username}`}>
              <StyledButton
                variant={'contained'}
                color={'primary'}
                style={{ marginLeft: '5px' }}
                size={matches ? 'medium' : 'small'}
              >
                View
              </StyledButton>
            </Link>
          </ListItemSecondaryAction>
        </ListItem>
      )
    })
  }

  return (
    <StyledWrapper marginTop={2}>

      <Box
        display={'flex'}
        style={{ padding: '0' }}
      >
        <StyledSearchField
          label={'Search'}
          variant={'outlined'}
          name={'searchValue'}
          value={form.values.searchValue}
          onChange={form.handleChange}
          size={matches ? 'medium' : 'small'}
        />
      </Box>

      <List>
        {renderedUsers()}
      </List>

    </StyledWrapper>
  )
}

const StyledWrapper = withStyles({
  root: {
    backgroundColor: 'white'
  }
})(Box)

const StyledButton = withStyles({
  root: {
    borderRadius: 0,
    boxShadow: 'none'
  }
})(Button)

const StyledFollowBtn = withStyles({
  root: {
    minWidth: '80px'
  }
})(StyledButton)

const StyledUnfollowBtn = withStyles({
  root: {
    minWidth: '80px'
  }
})(StyledButton)

const StyledSearchField = withStyles({
  root: {
    flex: '1',
  },
  formControl: {
    
  }
})(TextField)

const mapStateToProps = (state) => ({
  users: state.users,
  auth: state.auth
})

const mapDispatchToProps = {
  follow,
  unfollow
}

export default connect(mapStateToProps, mapDispatchToProps)(UsersPage)
