import { Box, Drawer, withStyles, Button, List, ListItem, Typography } from '@material-ui/core'
import React from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { toggleSideMenuVisiblity, toggleLoginModalVisibility, toggleRegisterModalVisibility } from 'store/slices/uiSlice'
import { logout } from 'store/slices/authSlice'

const SideMenu = (props) => {
  return (
    <Drawer
      open={props.sideMenuVisibility}
      anchor={'right'}
      onClose={() => props.toggleSideMenuVisiblity({})}

    >
      <Box
        justifyContent={'space-between'}
        display={'flex'}
        flexDirection={'column'}
        minWidth={200}
        padding={1}
        height={'100%'}
      >
        <Box>
          <List>
            <Link to={'/home'} onClick={() => props.toggleSideMenuVisiblity({})}>
              <ListItem button>
                <Typography variant={'h6'}>
                  Home
                </Typography>
              </ListItem>
            </Link>
            {props.auth.isAuthenticated && (
              <Link
                onClick={() => props.toggleSideMenuVisiblity({})}
                to={`/profile/${props.users.find(u => u.id === props.auth.userId).username}`}
              >
                <ListItem button>
                  <Typography variant={'h6'}>
                    Profile
                  </Typography>
                </ListItem>
              </Link>
            )}
            <Link to={'/users'} onClick={() => props.toggleSideMenuVisiblity({})}>
              <ListItem button>
                <Typography variant={'h6'}>
                  Users
                </Typography>
              </ListItem>
            </Link>
          </List>
        </Box>

        <Box display={'flex'} flexDirection={'column'}>
          {props.auth.isAuthenticated ? (
            <StyledButton
              color={'secondary'}
              onClick={() => props.logout({})}
              variant={'contained'}
            >
              Logout
            </StyledButton>
          ) : (
            <StyledButton
              variant={'contained'}
              onClick={() => props.toggleLoginModalVisibility({})}
              style={{ color: 'white', backgroundColor: '#2a9d8f' }}
            >
              Login
            </StyledButton>
          )}
          <StyledButton
            color={'primary'}
            variant={'contained'}
            onClick={() => props.toggleRegisterModalVisibility({})}
            style={{ marginTop: '10px' }}
          >
            Register
          </StyledButton>
        </Box>

      </Box>
    </Drawer>
  )
}

const StyledButton = withStyles({
  root: {
    borderRadius: 0,
    boxShadow: 'none'
  }
})(Button)

const mapStateToProps = (state) => ({
  sideMenuVisibility: state.ui.sideMenuVisibility,
  auth: state.auth,
  users: state.users
})

const mapDispatchToProps = {
  toggleSideMenuVisiblity,
  toggleLoginModalVisibility,
  toggleRegisterModalVisibility,
  logout
}

export default connect(mapStateToProps, mapDispatchToProps)(SideMenu)
