import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography } from "@material-ui/core"
import { useEffect } from "react"
import { useState } from "react"

const About = () => {
  const [opened, setOpened] = useState(true)

  useEffect(() => {
    const alreadyShown = localStorage.getItem('shown') === 'yes'
    if (alreadyShown) {
      setOpened(false)
    } else {
      localStorage.setItem('shown', 'yes')
    }
  }, [])

  return (
    <Dialog
      open={opened}
      onClose={() => setOpened(false)}
    >
      <DialogTitle>
        <Typography variant={'h6'}>
          About this Website
        </Typography>
      </DialogTitle>
      <DialogContent dividers>
        <Typography gutterBottom>
          Welcom to SocialMedia app, made using latest tecknologes, including
          React, Redux, Material UI and others.
        </Typography>
        <Typography gutterBottom>
          It has a wide range of features, like create, deleting, editing posts,
          auth system, subscribtions system and many more.
        </Typography>
        <Typography gutterBottom>
          Note: this website does not use any backend. All the data is stored within
          JavaScript code and will be lost once you reload the page.
        </Typography>
        <Typography gutterBottom>
          Good luck!!!
        </Typography>
      </DialogContent>
      <DialogActions>
        <Button
          variant={'outlined'}
          color={'primary'}
          onClick={() => setOpened(false)}
          size={'small'}
        >
          Close
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default About
