import { Avatar, Box, Card, List, ListItem, Tab, Tabs, Typography, withStyles, ListItemAvatar, ListItemText, Button, ListItemSecondaryAction } from '@material-ui/core'
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { v4 } from 'uuid'
import { Link, useParams } from 'react-router-dom'
import { follow, unfollow } from 'store/slices/usersSlice'
import Posts from './Posts'
import { toggleEditProfileModalVisibility } from 'store/slices/uiSlice'
import { Alert } from '@material-ui/lab'

const ProfilePage = (props) => {
  const [tab, setTab] = useState(0)
  const { username } = useParams()

  const profileUser = props.users.find(u => u.username == username)

  // if user is not found
  if (!profileUser) {
    return (
      <h1>Not found</h1>
    )
  }

  let currentUser

  if (props.auth.isAuthenticated) {
    currentUser = props.users.find(u => u.id === props.auth.userId)
  } else {
    currentUser = null
  }

  let currentUserFollowedToProfileUser = null

  if (currentUser?.followings.includes(profileUser.id)) {
    currentUserFollowedToProfileUser = true
  } else {
    currentUserFollowedToProfileUser = false
  }

  const viewBtn = (
    <Button
      variant={'contained'}
      color={'primary'}
      label={'view'}
    >
      View
    </Button>
  )

  const renderedFollowers = () => {
    const filteredUsers = props.users.filter(user => {
      if (user.followings.includes(profileUser.id)) {
        return true
      } else {
        return false
      }
    })

    return filteredUsers.map(u => (
      <ListItem button key={v4()}>
        <ListItemAvatar>
          <Avatar/>
        </ListItemAvatar>
        <ListItemText primary={u.username} />
        <ListItemSecondaryAction>
          <Link to={`/profile/${u.username}`}>
            {viewBtn}
          </Link>
        </ListItemSecondaryAction>
      </ListItem>
    ))
  }

  const renderedFollowings = () => {
    const filteredUsers = props.users.filter(user => {
      if (profileUser.followings.includes(user.id)) {
        return true
      } else {
        return false
      }
    })

    return filteredUsers.map(u => (
      <ListItem button key={v4()}>
        <ListItemAvatar>
          <Avatar src={u.avatarUrl} />
        </ListItemAvatar>
        <ListItemText primary={u.username} />
        <ListItemSecondaryAction>
          <Link to={`/profile/${u.username}`}>
            <Button
              variant={'contained'}
              color={'primary'}
              label={'view'}
            >
              View
            </Button>
          </Link>
        </ListItemSecondaryAction>
      </ListItem>
    ))
  }

  const filteredPosts = props.posts.filter(p => p.authorId === profileUser.id)


  return (
    <Box>
      <StyledProfile>

        <StyledHeader>
          <Box
            display={'flex'}
            alignItems={'center'}
            paddingLeft={2}
            paddingRight={2}
          >
            <Avatar src={profileUser.avatarUrl} />
            <Box marginLeft={1}>
              <Typography variant={'h6'}>
                {profileUser.username}
              </Typography>
            </Box>
          </Box>
          <Box>
            {props.auth.isAuthenticated && currentUser?.id !== profileUser.id && (
              (currentUserFollowedToProfileUser ? (
                <Button
                  variant={'contained'}
                  color={'secondary'}
                  onClick={() => props.unfollow({ from: currentUser.id, to: profileUser.id })}
                >
                  unfollow
                </Button>
              ) : (
                <Button
                  variant={'contained'}
                  style={{ backgroundColor: 'lightgreen' }}
                  onClick={() => props.follow({ from: currentUser.id, to: profileUser.id })}
                >
                  follow
                </Button>
              ))  
            )}
            {/* If current user is viewing himself/herself */}
            {props.auth.isAuthenticated && currentUser?.id === profileUser.id && (
              <Button
                variant={'outlined'}
                color={'primary'}
                onClick={() => props.toggleEditProfileModalVisibility({})}
              >
                Edit
              </Button>
            )}
          </Box>
        </StyledHeader>

      {/* About */}
        <Box marginTop={3}>
          <Typography variant={'body1'}>
            {profileUser.about}
          </Typography>
        </Box>
      {/* About */}

        <Box marginTop={1}>
          <Tabs
            value={tab}
            onChange={(e, v) => setTab(v)}
            indicatorColor="primary"
            textColor="primary"
            // centered
          >
            <Tab label={'Posts'} />
            <Tab label={'Followers'} />
            <Tab label={'Followings'} />
          </Tabs>
          <Box marginTop={2}>
          {/* Posts===================================  */}
            {tab === 0 && <>
              <Posts posts={filteredPosts} />
              {filteredPosts.length === 0 && (
                <Alert
                  severity={'info'}
                  action={
                    props.auth.isAuthenticated && currentUser?.id === profileUser.id && (
                      <Link to={'/home'}>
                        <Button color={'primary'} variant={"outlined"}>
                          create
                        </Button>
                      </Link>
                    )
                  }
                >
                  No posts created!
                </Alert>
              )}
            </>}
          {/* Posts===================================  */}


          {/* Followers=============================== */}
            {tab === 1 && <>
              <List>
                {renderedFollowers()}
              </List>
              {renderedFollowers().length === 0 && (
                <Alert severity={'info'}>
                  No followers!
                </Alert>
              )}
            </>}
          {/* Followers=============================== */}

          {/* Followings================================ */}
            {tab === 2 && <>
              <List>
                {renderedFollowings()}
              </List>
              {renderedFollowings().length === 0 && (
                <Alert severity={'info'}>
                  No followings!
                </Alert>
              )}
            </>}
          {/* Followings================================ */}
          </Box>
        </Box>

      </StyledProfile>
    </Box>
  )
}

const StyledProfile = withStyles({
  root: {
    maxWidth: '600px',
    margin: '15px auto',
    padding: 20,
    // border: '1px solid rgba(0, 0, 0, .2)',
    boxShadow: '0 0 30px rgba(0, 0, 0, 0.1)'
  }
})(Card)

const StyledHeader = withStyles({
  root: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between'
  }
})(Box)

const mapStateToProps = (state) => {
  return {
    users: state.users,
    auth: state.auth,
    posts: state.posts
  }
}

const mapDispatchToProps = {
  follow,
  unfollow,
  toggleEditProfileModalVisibility
}

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePage)
