import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField, Typography } from '@material-ui/core'
import { useFormik } from 'formik'
import pt from 'prop-types'
import { connect } from 'react-redux'
import { toggleEditPostVisiblity } from 'store/slices/uiSlice'
import { editPost } from 'store/slices/postsSlice'

const EditPost = (props) => { 
  const form = useFormik({
    initialValues: {
      body: '',
      tags: ''
    },
    onSubmit(v) {
      const body = v.body
      const tags = v.tags.split(' ')
      if (body.trim() === '' || v.tags.trim() === '') return
      props.editPost({
        postId: props.editingPostId,
        newPost: {
          body, tags
        }
      })
      form.resetForm()
      props.toggleEditPostVisiblity({})
    }
  })

  return (
    <Dialog open={props.editPostVisibility} onClose={() => props.toggleEditPostVisiblity({})}>
      <DialogTitle>
        <Typography align={'center'} variant={'h5'}>
          Edit Post
        </Typography>
      </DialogTitle>
      <DialogContent>
        <TextField
          name={'body'}
          value={form.values.body}
          onChange={form.handleChange}
          variant={'outlined'}
          size={'small'}
          fullWidth
          multiline
          rows={2}
          label={'Body'}
        />
        <Box marginTop={1}>
          <TextField
            name={'tags'}
            value={form.values.tags}
            onChange={form.handleChange}
            variant={'outlined'}
            size={'small'}
            fullWidth
            label={'Space seperated tags'}
          />
        </Box>
      </DialogContent>
      <DialogActions>
        <Button
          variant={'contained'}
          onClick={form.handleSubmit}
          color={'primary'}
        >
          Edit
        </Button>
        <Button
          variant={'contained'}
          onClick={() => props.toggleEditPostVisiblity({})}
          color={'secondary'}
        >
          cancel
        </Button>
      </DialogActions>
    </Dialog>
  )
}

EditPost.propTypes = {
  
}

const mapStateToProps = (state) => ({
  editPostVisibility: state.ui.editPostVisibility,
  editingPostId: state.ui.editingPostId,
  auth: state.auth
})

const mapDispatchToProps = {
  toggleEditPostVisiblity,
  editPost
}


export default connect(mapStateToProps, mapDispatchToProps)(EditPost)
