import { Box, Button, Typography } from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import CreatePost from './CreatePost'
import Posts from './Posts'

const HomePage = (props) => {
  let posts

  // if user is authenticated, get only posts from subscribed users
  if (props.auth.isAuthenticated) {
    const currentUser = props.users.find(u => u.id == props.auth.userId)
    posts = props.posts.filter(p => currentUser.followings.includes(p.authorId))
  } else {
    // otherwise get all posts
    posts = props.posts
  }

  return (
    <Box>
      <CreatePost />
      <Box marginTop={5}>
        <Posts posts={posts} />
      </Box>
      {posts.length === 0 && <>
        <Alert
          severity={'info'}
          action={<Link to={'/users'}>
            <Button variant={'outlined'} color={'primary'}>
              Follow
            </Button>
          </Link>}
        >
          Follow users to see their posts!
        </Alert>
      </>}
    </Box>
  )
}

const mapStateToProps = (state) => ({
  posts: state.posts,
  auth: state.auth,
  users: state.users
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(HomePage)
