import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Snackbar, TextField, Typography } from "@material-ui/core"
import { useFormik } from "formik"
import { connect } from 'react-redux'
import { toggleLoginModalVisibility } from "store/slices/uiSlice"
import { login } from "store/slices/authSlice"
import { Alert } from "@material-ui/lab"
import { useState } from "react"

const LoginModal = (props) => {
  const [userDoesNotExistSnackbar, setUserDoesNotExistSnackbar] = useState(false)
  const form = useFormik({
    initialValues: {
      username: '',
      password: ''
    },
    onSubmit(v) {
      const foundUser = props.users.find(user => {
        if (user.username === v.username && user.password === v.password) {
          return true
        }
        return false
      })
      if (foundUser) {
        props.login({ userId: foundUser.id })
        form.resetForm()
        props.toggleLoginModalVisibility()
      } else {
        setUserDoesNotExistSnackbar(true)
      }
    }
  })

  return <>
    <Dialog
      open={props.loginModalVisibility} 
      onClose={() => props.toggleLoginModalVisibility({})}
    >
      <DialogTitle>
        <Typography align={'center'} variant={'h5'}>
          Login
        </Typography>
      </DialogTitle>
      <DialogContent>

        <Box marginTop={1}>
          <TextField
            name={'username'}
            label={'Username'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.username}
            onChange={form.handleChange}
          />
        </Box>

        <Box marginTop={1}>
          <TextField
            name={'password'}
            label={'Password'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.password}
            onChange={form.handleChange}
          />
        </Box>

      </DialogContent>
      <DialogActions>
        <Button
          color={'primary'}
          variant={'contained'}
          onClick={form.handleSubmit}
        >
          Login
        </Button>
        <Button
          color={'secondary'}
          variant={'contained'}
          onClick={() => props.toggleLoginModalVisibility({})}
        >
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
    <Snackbar autoHideDuration={2000} open={userDoesNotExistSnackbar} onClose={() => setUserDoesNotExistSnackbar(false)}>
      <Alert variant={'filled'} severity={'error'}>
        User does not exist
      </Alert>
    </Snackbar>
  </>
}

const mapStateToProps = (state) => ({
  loginModalVisibility: state.ui.loginModalVisibility,
  users: state.users
})

const mapDispatchToProps = {
  toggleLoginModalVisibility,
  login
}


export default connect(mapStateToProps, mapDispatchToProps)(LoginModal)
