import { createSlice, nanoid } from "@reduxjs/toolkit"

// properties: id, username, password, about, avatarUrl, followings

const initialState = [
  {
    id: 'id112',
    username: 'Aziz',
    password: '2',
    about: 'I love chocolate',
    avatarUrl: 'data:image/jpeg;base64,',
    followings: []
  },
  {
    id: 'id113',
    username: 'Zarina',
    password: '3',
    about: 'Hello, welcom to page!!!',
    avatarUrl: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSs5oyA2NrqWSYtzn_WGADisN0MJh9wSX1ZJw&usqp=CAU',
    followings: ['id112']
  },
]

const usersSlice = createSlice({
  name: 'users',
  initialState,
  reducers: {
    createUser(state, action) {
      const { id, username, password, about, avatarUrl } = action.payload
      state.push({
        id,
        username,
        password,
        about: about.trim() ? about.trim() : null,
        avatarUrl: avatarUrl.trim() ? avatarUrl.trim() : null,
        followings: []
      })
    },
    deleteUser(state, action) {
      const { userId } = action.payload
      state.splice([state.findIndex(u => u.id === userId)], 1)
    },
    follow(state, action) {
      const { from, to } = action.payload
      state[state.findIndex(u => u.id === from)].followings.push(to)
    },
    unfollow(state, action) {
      const { from, to } = action.payload
      const followings = state.find(user => user.id === from).followings
      followings.splice(followings.findIndex(f => f === to), 1)
    },
    editUser(state, action) {
      const { userId, newUser } = action.payload
      const { username, password, about, avatarUrl } = newUser
      const user = state[state.findIndex(u => u.id === userId)]
      user.username = username
      user.password = password
      user.about = about.trim() ? about.trim() : null
      user.avatarUrl = avatarUrl.trim() ? avatarUrl.trim() : null
    }
  }
})

const { actions, reducer } = usersSlice

export const {
  
  createUser,
  deleteUser,
  follow,
  unfollow,
  editUser

} = actions

export default reducer
