import { Box, Button, IconButton, Typography, useMediaQuery, useTheme, withStyles } from "@material-ui/core"
import { connect } from "react-redux"
import { toggleLoginModalVisibility, toggleRegisterModalVisibility } from "store/slices/uiSlice"
import { logout } from "store/slices/authSlice"
import { Link } from "react-router-dom"
import MenuIcon from '@material-ui/icons/Menu'
import SideMenu from "./SideMenu"
import { toggleSideMenuVisiblity } from 'store/slices/uiSlice'

const Header = (props) => {
  const matches = useMediaQuery('(min-width: 768px)')
  const theme = useTheme()
  
  return (
    <StyledHeader
      display={'flex'}
      alignItems={'center'}
      justifyContent={'space-between'}
      padding={1}
    >
    {/* Left */}
      <Typography
        variant={matches ? 'h4' : 'h5'}
        style={{ color: '#3d405b', fontWeight: '600' }}
      >
        SocialMedia
      </Typography>
    {/* Left */}

    {/* Middle */}
      {matches && (
        <Box
          display={'flex'}
        >
          <Link to={'/home'}>
            <Button
            >
              <Typography variant={'h6'}>
                Home
              </Typography>
            </Button>
          </Link>
          {props.isAuthenticated && (
            <Link to={`/profile/${props.currentUser?.username}`}>
              <Button
              >
                <Typography variant={'h6'}>
                  Profile
                </Typography> 
              </Button>
            </Link>
          )}
          <Link to={`/users`}>
            <Button
            >
              <Typography variant={'h6'}>
                Users
              </Typography> 
            </Button>
          </Link>
        </Box>
      )}
    {/* Middle */}

    {/* Right */}
      {matches ? (
        <Box>
          {props.isAuthenticated ? (
            <StyledButton
              onClick={() => props.logout({})}
              variant={'contained'}
              style={{ backgroundColor: '#f50057', color: 'white' }}
            >
              Logout
            </StyledButton>
          ) : (
            <StyledButton
              variant={'contained'}
              onClick={() => props.toggleLoginModalVisibility({})} 
              style={{ backgroundColor: '#2a9d8f', color: 'white' }}
            >
              Login
            </StyledButton>
          )}
          <StyledButton
            color={'primary'}
            variant={'contained'}
            style={{ marginLeft: '5px' }}
            onClick={() => props.toggleRegisterModalVisibility({})} 
          >
            Register
          </StyledButton>
        </Box>
      ) : <>
        <IconButton size={'small'}>
          <MenuIcon
            onClick={() => props.toggleSideMenuVisiblity({})}
            style={{ fontSize: '30px' }}
          />
        </IconButton>
        <SideMenu />
      </>}
    {/* Right */}
    </StyledHeader>
  )
}

const StyledHeader = withStyles({
  root: {
    backgroundColor: '#e9c46a',
    boxShadow: '0 0 10px rgba(0, 0, 0, .2)'
  }
})(Box)

const StyledButton = withStyles({
  root: {
    borderRadius: '0',
    boxShadow: 'none',
  }
})(Button)

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  currentUser: state.users.find(u => u.id === state.auth.userId)
})

const mapDispatchToProps = {
  toggleLoginModalVisibility,
  toggleRegisterModalVisibility,
  toggleSideMenuVisiblity,
  logout
}


export default connect(mapStateToProps, mapDispatchToProps)(Header)
