import { createSlice } from "@reduxjs/toolkit"
import moment from "moment"
import { v4 } from "uuid"

// properties: id, authorId, body, tags, created, likes, comments

const initialState = [
  {
    id: 'dalers-post',
    authorId: 'id112',
    body: `React is Awesome`,
    tags: ['React', 'Vue', 'Angular'],
    created: moment().format('MMMM Do YYYY, h:mm:ss a'),
    likes: [],
    comments: [
      { authorId: 'id113', body: 'Great!' }
    ]
  },
  {
    id: 'id1112',
    authorId: 'id113',
    body: `I anybody here?`,
    tags: ['Uzbekistan', 'Tashkent'],
    created: moment().format('MMMM Do YYYY, h:mm:ss a'),
    likes: [],
    comments: []
  },
]

const postsSlice = createSlice({
  name: 'posts',
  initialState,
  reducers: {
    createPost(state, action) {
      const { authorId, body, tags } = action.payload
      state.push({
        id: v4(),
        authorId,
        body,
        tags,
        created: moment().format('MMMM Do YYYY, h:mm:ss a'),
        likes: [],
        comments: [],
      })
    },
    createComment(state, action) {
      const { authorId, postId, body } = action.payload
      state[state.findIndex(p => p.id === postId)].comments.push({ authorId, body })
    },
    likePost(state, action) {
      const { postId, userId } = action.payload
      state[state.findIndex(p => p.id === postId)].likes.push(userId)
    },
    unlikePost(state, action) {
      const { postId, userId } = action.payload
      const likes = state[state.findIndex(p => p.id === postId)].likes
      likes.splice(likes.findIndex(l => l === userId), 1)
    },
    deletePost(state, action) {
      const { postId } = action.payload
      console.log(postId)
      state.splice(state.findIndex(p => p.id === postId), 1)
    },
    editPost(state, action) {
      const { postId, newPost } = action.payload
      const { body, tags } = newPost
      const post = state.find(p => p.id === postId)
      post.body = body
      post.tags = tags
    }
  }
})

const { actions, reducer } = postsSlice

export const {

  createPost,
  likePost,
  unlikePost,
  createComment,
  deletePost,
  editPost

} = actions

export default reducer
