import { createSlice } from "@reduxjs/toolkit"


const initialState = {
  loginModalVisibility: false,
  registerModalVisibility: false,
  sideMenuVisibility: false,
  editPostVisibility: false,
  editProfileModalVisibility: false,
  editingPostId: null
}

const uiSlice = createSlice({
  name: 'ui',
  initialState,
  reducers: {
    toggleLoginModalVisibility: (state, action) => {
      state.loginModalVisibility = !state.loginModalVisibility
    },
    toggleRegisterModalVisibility: (state, action) => {
      state.registerModalVisibility = !state.registerModalVisibility
    },
    toggleSideMenuVisiblity(state, action) {
      state.sideMenuVisibility = !state.sideMenuVisibility
    },
    toggleEditPostVisiblity(state, action) {
      state.editPostVisibility = !state.editPostVisibility
    },
    setEditingPostId(state, action) {
      state.editingPostId = action.payload.to
    },
    toggleEditProfileModalVisibility(state, action) {
      state.editProfileModalVisibility = !state.editProfileModalVisibility
    }
  }
})

const { actions, reducer } = uiSlice

export const {

  toggleLoginModalVisibility,
  toggleRegisterModalVisibility,
  toggleSideMenuVisiblity,
  toggleEditPostVisiblity,
  setEditingPostId,
  toggleEditProfileModalVisibility

} = actions

export default reducer
