import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Snackbar, TextField, Typography, withStyles } from "@material-ui/core"
import { Alert } from "@material-ui/lab"
import { useFormik } from "formik"
import { useState } from "react"
import { connect } from 'react-redux'
import { toggleRegisterModalVisibility } from "store/slices/uiSlice"
import { createUser } from "store/slices/usersSlice"
import { login } from "store/slices/authSlice"
import { nanoid } from "@reduxjs/toolkit"


const RegisterModal = (props) => {
  const [emptyTextError, setEmptyTextError] = useState(false)
  const [userAlreadyExistsSnackbarVisibility, setUserAlreadyExistsSnackbarVisibility] = useState(false)

  const form = useFormik({
    initialValues: {
      username: '',
      password: '',
      about: '',
      avatarUrl: ''
    },
    onSubmit(v) {
      const { username, password, about, avatarUrl } = v

      if (username.trim() === '' || password.trim() === '') {
        setEmptyTextError(true)
        return
      }

      // check if user with the same password or username does not exist
      let foundUserWithTheSamePasswordOrUsername = 
        props.users.find(u => u.username === username || u.password === username)
      
      if (foundUserWithTheSamePasswordOrUsername) {
        setUserAlreadyExistsSnackbarVisibility(true)
        return
      }

      const id = nanoid()
      props.createUser({
        username, password, about, avatarUrl, id
      })
      props.login({ userId: id })
      props.toggleRegisterModalVisibility({})
      form.resetForm()
    }
  })

  return <>
    <Dialog
      open={props.registerModalVisibility} 
      onClose={() => props.toggleRegisterModalVisibility({})}
    >
      <DialogTitle>
        <Typography variant={'h5'} align={'center'}>
          Register
        </Typography>
      </DialogTitle>
      <DialogContent>

        <Box marginTop={1}>
          <StyledTextField
            name={'username'}
            label={'Username'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.username}
            onChange={form.handleChange}
            error={emptyTextError ? true : false}
          />
        </Box>

        <Box marginTop={1}>
          <StyledTextField
            name={'password'}
            label={'Password'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.password}
            onChange={form.handleChange}
            error={emptyTextError ? true : false}
          />
        </Box>

        <Box marginTop={1}>
          <StyledTextField
            name={'about'}
            label={'About'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.about}
            onChange={form.handleChange}
          />
        </Box>

        <Box marginTop={1}>
          <StyledTextField
            name={'avatarUrl'}
            label={'Avatar URL'}
            fullWidth
            variant={'outlined'}
            size={'small'}
            value={form.values.avatarUrl}
            onChange={form.handleChange}
          />
        </Box>

      </DialogContent>
      <DialogActions>
        <Button
          color="primary"
          variant={'contained'}
          onClick={form.handleSubmit}
        >
          Register
        </Button>
        <Button
          color={'secondary'}
          variant={'contained'}
          onClick={() => {
            form.resetForm()
            setEmptyTextError(false)
            props.toggleRegisterModalVisibility({})
          }}
        >
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
    <Snackbar
      open={userAlreadyExistsSnackbarVisibility}
      onClose={() => setUserAlreadyExistsSnackbarVisibility(false)}
      autoHideDuration={2000}
    >
      <Alert severity={'error'} variant={'filled'}>
        User already exists!
      </Alert>
    </Snackbar>
  </>
}

const StyledTextField = withStyles({
  root: {
    '& > *': {
      borderRadius: '0'
    }
  }
})(TextField)

const mapStateToProps = (state) => ({
  registerModalVisibility: state.ui.registerModalVisibility,
  users: state.users
})

const mapDispatchToProps = {
  toggleRegisterModalVisibility,
  createUser,
  login
}


export default connect(mapStateToProps, mapDispatchToProps)(RegisterModal)
