import { useMediaQuery } from '@material-ui/core'
import { Box, TextField, Button, withStyles, Snackbar } from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { useFormik } from 'formik'
import { useState } from 'react'
import { connect } from 'react-redux'
import { createPost } from 'store/slices/postsSlice'


const CreatePost = (props) => {
  const [snackbarVisibility, setSnackbarVisibility] = useState(false)
  const matches = useMediaQuery('(min-width: 768px)')

  const form = useFormik({
    initialValues: {
      body: '',
      tags: '',
    },
    onSubmit(v) {
      if (v.body.trim() === '' || v.tags.trim() === '') return
      if (!props.auth.isAuthenticated) return
      
      const post = {
        authorId: currentUser.id,
        body: v.body,
        tags: v.tags.split(' '),
      }

      props.createPost(post)
      form.resetForm()
      setSnackbarVisibility(true)
    }
  })

  let currentUser

  if (props.auth.isAuthenticated) {
    currentUser = props.users.find(u => u.id === props.auth.userId)
  } else {
    currentUser = null
  }

  return <>
    <StyledForm component={'form'} marginTop={2}>
      <Box>
        <TextField
          name={'body'}
          value={form.values.body}
          onChange={form.handleChange}
          label={'Post body'}
          variant={'outlined'}
          fullWidth
          size={'small'}
          multiline
          rows={5}
          disabled={props.auth.isAuthenticated ? false : true}
        />
      </Box>
      <Box marginTop={1}>
        <TextField
          name={'tags'}
          value={form.values.tags}
          onChange={form.handleChange}
          variant={'outlined'}
          fullWidth
          size={'small'}
          label={'List tags with spaces'}
          disabled={props.auth.isAuthenticated ? false : true}
        />
      </Box>
      <Box marginTop={1}>
        <Button
          variant={'contained'}
          color={'primary'}
          fullWidth
          onClick={form.handleSubmit}
          disabled={props.auth.isAuthenticated ? false : true}
          size={matches ? 'medium' : 'small'}
        >
          create post
        </Button>
      </Box>
    </StyledForm>
    <Snackbar
      open={snackbarVisibility}
      onClose={() => setSnackbarVisibility(false)}
      autoHideDuration={2000}
    >
      <Alert variant={'filled'} severity={'success'}>
        Post Created successfully
      </Alert>
    </Snackbar>
  </>
}

const StyledForm = withStyles({
  root: {
    backgroundColor: 'white'
  }
})(Box)

const mapStateToProps = (state) => ({
  auth: state.auth,
  users: state.users
})

const mapDispatchToProps = {
  createPost
}

export default connect(mapStateToProps, mapDispatchToProps)(CreatePost)
