import { Avatar, Badge, Box, Card, CardActions, CardContent, CardHeader, Chip, Collapse, Divider, IconButton, List, ListItem, TextField, Typography, withStyles, Button, Menu, MenuItem } from '@material-ui/core'
import pt from 'prop-types'
import { connect } from 'react-redux'
import FavouriteIcon from '@material-ui/icons/Favorite'
import CommentIcon from '@material-ui/icons/Comment'
import { likePost, unlikePost } from 'store/slices/postsSlice'
import { useState } from 'react'
import { createComment, deletePost } from 'store/slices/postsSlice'
import { useMediaQuery } from '@material-ui/core'
import MoreIcon from '@material-ui/icons/MoreVert'
import { toggleEditPostVisiblity, setEditingPostId } from 'store/slices/uiSlice'
import { Link } from 'react-router-dom'


const Post = (props) => {
  const [commentsOpened, setCommentsOpened] = useState(false)
  const [commentValue, setCommentValue] = useState('')
  const [menuOpened, setMenuOpened] = useState(false)
  
  const matches = useMediaQuery('(min-width: 768px)')

  const user = props.users.find(u => u.id === props.authorId)

  let currentUserLikedThisPost = 
    props.likes.includes(props.auth.userId) ? true : false

  const handleLike = () => {
    if (props.auth.isAuthenticated) {
      // check if current user already liked this post
      if (!currentUserLikedThisPost) {
        props.likePost({ postId: props.id, userId: props.auth.userId })
      } else {
        // otherwise remove like
        props.unlikePost({ postId: props.id, userId: props.auth.userId })
      }
    }
  }

  const handleCreateComment = () => {
    if (props.auth.isAuthenticated && commentValue.trim() !== '') {
      props.createComment({ authorId: props.auth.userId, postId: props.id, body: commentValue })
      setCommentValue('')
    }
  }

  let postBelongsToCurrentUser

  if (props.auth.isAuthenticated) {
    if (props.authorId === props.auth.userId) {
      postBelongsToCurrentUser = true
    } else {
      postBelongsToCurrentUser = false
    }
  } else {
    postBelongsToCurrentUser = false
  }

  const renderedComments = props.comments.map(comment => {
    const author = props.users.find(u => u.id === comment.authorId)

    return (
      <ListItem button>
        <Box
          display={'flex'}
          justifyContent={'flex-start'}
          alignItems={'center'}
          width={'100%'}
        >
          <Box marginRight={1}>
            <Typography variant={'h6'}>{author.username}</Typography>
          </Box> - 
          <Box marginLeft={1}>
            <Typography variant={'body2'}>
              {comment.body}
            </Typography>
          </Box>
        </Box>
      </ListItem>
    )
  })

  return (
    <StyledCard>
      <CardHeader
        avatar={
          <Avatar src={user.avatarUrl} />
        }
        title={
          <Link to={`/profile/${user.username}`}>
            <Typography variant={matches ? 'h5' : 'h6'}>
              {user.username}
            </Typography>
          </Link>
        }
        subheader={props.created}
        action={ postBelongsToCurrentUser ? (
            <IconButton id={'moreBtn'} onClick={() => setMenuOpened(true)}>
              <MoreIcon />
            </IconButton>
          ) : null
        }
      />
      <StyledMenu
        anchorEl={document.getElementById('moreBtn')}
        open={menuOpened}
        onClose={() => setMenuOpened(false)}
        keepMounted
        id={'menu'}
      >
        <MenuItem
          onClick={() => props.deletePost({ postId: props.id })}
          style={{ color: 'red' }}
        >
          Delete
        </MenuItem>
        <MenuItem
          onClick={() => {
            props.toggleEditPostVisiblity({})
            props.setEditingPostId({ to: props.id })
          }}
        >
          Edit
        </MenuItem>
      </StyledMenu>
      <CardContent>
        <Typography variant={matches ? 'h5' : 'h6'}>
          {props.body}
        </Typography>
        <Box
          display={'flex'}
          flexWrap={'wrap'}
          marginTop={3}
        >
          {props.tags.map((t, i) => <>
            <Box marginLeft={i !== 0 && 1}>
              <Chip
                label={t}
                variant={'outlined'}
                size={matches ? 'middle' : 'small'}
              />
            </Box>
          </>)}
        </Box>
      </CardContent>
      <StyledFooter>
        <Box
          display={'flex'}
          alignItems={'center'}
        >
          <StyledCommentField
            variant={'outlined'}
            name={'comment'}
            placeholder={'comment'}
            value={commentValue}
            onChange={(e) => setCommentValue(e.target.value)}
            disabled={props.auth.isAuthenticated ? false : true}
            size={'small'}
          />
          <StyledButton
            color={'primary'}
            variant={'outlined'}
            onClick={() => handleCreateComment()}
            disabled={props.auth.isAuthenticated ? false : true}
            size={matches ? 'medium' : 'small'}
          >
            Leave
          </StyledButton>
        </Box>
        <Box display={'flex'} alignItems={'center'}>
          <IconButton onClick={handleLike}>
            <Badge
              badgeContent={props.likes.length}
              color={'secondary'}
            >
              <FavouriteIcon
                color={
                  currentUserLikedThisPost ? 'secondary' : 'default'
                }
              />
            </Badge>
          </IconButton>
          <IconButton onClick={() => setCommentsOpened(!commentsOpened)}>
            <Badge badgeContent={props.comments.length} color={'primary'}>
              <CommentIcon />
            </Badge>
          </IconButton>
        </Box>
      </StyledFooter>
      <Collapse in={commentsOpened} timeout="auto" unmountOnExit>
        <List>
          {renderedComments}
        </List>
      </Collapse>
    </StyledCard>
  )
}

const StyledMenu = withStyles({
  root: {
    '& MuiList-padding': {
      padding: 0
    }
  }
})(Menu)

const StyledCard = withStyles({
  root: {
    marginTop: '50px',
    boxShadow: 'none',
    boxShadow: '0 0 10px rgba(0, 0, 0, 0.2)',
    borderRadius: '0'
  }
})(Card)

const StyledCommentField = withStyles({
  root: {

    '& > *': {
      borderRadius: '0',
      padding: '0'
    }
  }
})(TextField)

const StyledButton = withStyles({
  root: {
    borderRadius: '0'
  }
})(Button)

const StyledFooter = withStyles({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center'
  }
})(CardActions)

Post.propTypes = {
  id: pt.string.isRequired,
  authorId: pt.string.isRequired,
  body: pt.string.isRequired,
  tags: pt.array.isRequired,
  created: pt.string.isRequired,
  comments: pt.array.isRequired,
  likes: pt.array.isRequired
}

const mapStateToProps = (state) => ({
  users: state.users,
  auth: state.auth
})

const mapDispatchToProps = {
  likePost,
  unlikePost,
  createComment,
  deletePost,
  toggleEditPostVisiblity,
  setEditingPostId
}


export default connect(mapStateToProps, mapDispatchToProps)(Post)
