import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField, Typography } from '@material-ui/core'
import { useFormik } from 'formik'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { toggleEditProfileModalVisibility } from 'store/slices/uiSlice'
import { editUser } from 'store/slices/usersSlice'

const EditProfileModal = (props) => {
  const history = useHistory()
  const form = useFormik({
    initialValues: {
      username: '',
      password: '',
      about: '',
      avatarUrl: ''
    },
    onSubmit(v) {
      const { username, password, about, avatarUrl } = v

      const foundUserWithTheSamePasswordOrUsername = props.users.find(
        u => u.username === username || u.password === password
      )

      if (foundUserWithTheSamePasswordOrUsername) {
        return
      }

      props.editUser({
        userId: props.auth.userId,
        newUser: {
          username, password, about, avatarUrl
        }
      })

      props.toggleEditProfileModalVisibility({})

      history.push(`/profile/${username}`)
    }
  })

  return (
    <Dialog 
      open={props.editProfileModalVisibility}
      onClose={() => props.toggleEditProfileModalVisibility({})}
    >
      <DialogTitle>
        <Typography variant={'h5'}>
          Edit Profile
        </Typography>
      </DialogTitle>
      <DialogContent>

        <Box marginTop={1}>
          <TextField
            label={'Username'}
            name={'username'}
            value={form.values.username}
            variant={'outlined'}
            onChange={form.handleChange}
            size={'small'}
            fullWidth
          />
        </Box>

        <Box marginTop={1}>
          <TextField
            label={'Password'}
            name={'password'}
            value={form.values.password}
            variant={'outlined'}
            onChange={form.handleChange}
            size={'small'}
            fullWidth
          />
        </Box>

        <Box marginTop={1}>
          <TextField
            label={'About'}
            name={'about'}
            value={form.values.about}
            variant={'outlined'}
            onChange={form.handleChange}
            size={'small'}
            fullWidth
          />
        </Box>

        <Box marginTop={1}>
          <TextField
            label={'Avatar URL'}
            name={'avatarUrl'}
            value={form.values.avatarUrl}
            variant={'outlined'}
            onChange={form.handleChange}
            size={'small'}
            fullWidth
          />
        </Box>

      </DialogContent>
      <DialogActions>
        <Button
          variant={'contained'}
          color={'primary'}
          onClick={form.handleSubmit}
        >
          save
        </Button>
        <Button
          variant={'contained'}
          color={'secondary'}
          onClick={() => props.toggleEditProfileModalVisibility({})}
        >
          cancel
        </Button>
      </DialogActions>
    </Dialog>
  )
}

const mapStateToProps = (state) => ({
  editProfileModalVisibility: state.ui.editProfileModalVisibility,
  auth: state.auth,
  users: state.users
})

const mapDispatchToProps = {
  toggleEditProfileModalVisibility,
  editUser
}

export default connect(mapStateToProps, mapDispatchToProps)(EditProfileModal)
